.cartContainer {
  display: flex;
  flex-direction: column;
  gap: 15px;
  padding: 70px 0 170px;
  position: relative;
  box-sizing: border-box;
  transform: rotate(0);
  min-height: 100vh;
}

.back {
  background-image: url("../../../public/back.png");
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  top: 0px;
  left: 0;
  height: 50px;
  width: 50px;
  appearance: none;
  border: none;
  outline: none;
  z-index: 11;
  background-color: rgba(220,220,220);
  cursor: pointer;
}

.clean {
  background-image: url("../../../public/delete.png");
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  top: 5px;
  right: 0;
  height: 35px;
  width: 35px;
  appearance: none;
  border: none;
  outline: none;
  z-index: 11;
  background-color: rgba(220,220,220);
  cursor: pointer;
}

.title {
  width: 100vw;
  box-sizing: border-box;
  background-color: rgba(220,220,220);
  position: absolute;
  top: -20px;
  right: 0;
  left: 0;
  z-index: 10;
  text-align: center;
  font-size: 24px;
  font-weight: 800;
  line-height: 50px;
}

.button {
  position: fixed;
  left: 5vw;
  right: 5vw;
  bottom: 15px;
  z-index: 99;
  background-color: #28a745;
  box-shadow: 0px 0px 20px 0px rgb(0 0 0 / 50%);
  border-radius: 10px;
  font-size: 24px;
  line-height: 50px;
  text-align: center;
  text-decoration: none;
  color: white;
  border: none;
}

.button:disabled {
  background-color: gray;
  cursor:not-allowed;
  box-shadow: none;
}

.clean:disabled {
  cursor:not-allowed;
}

.account {
    color: black;
    font-size: 16px;
    position: absolute;
    bottom: 100px;
    right: 15px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: flex-end;
    background-color: white;
    z-index: 10;
}

.delivery {
    width: fit-content;
}

.result {
    width: fit-content;
    font-weight: 600;
    font-size: 24px;
}