@keyframes popup {
    0% {
        transform: scale(1.1);
        -webkit-transform: scale(1.1);
    }
    33% {
        transform: scale(0.7);
        -webkit-transform: scale(0.7);
    }
    67% {
        transform: scale(1.1);
        -webkit-transform: scale(1.1);
    }
    100% {
        transform: scale(1);
        -webkit-transform: scale(1);
    }
}

.dishesContainer {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    gap: 15px;
    padding: 60px 0 80px;
}

.cartButton {
    position: fixed;
    will-change: transform;
    left: 5vw;
    right: 5vw;
    bottom: 15px;
    background-color: rgba(255, 0, 0, 0.75);
    box-shadow: 0px 0px 20px 0px rgb(0 0 0 / 50%);
    border-radius: 10px;
    font-size: 24px;
    line-height: 50px;
    text-align: center;
    text-decoration: none;
    color: white;
    animation: popup 1s ease-in-out;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}